.section {
  padding: 30px;
  margin-bottom: 40px;
}
.field-name-field-intro-text {
  margin-bottom: 40px;
}
.field-name-field-framework-text {
  /*  @include sans-light;
        @include font-size(20,24);
        color: $grey-label;*/
  margin-bottom: 35px;
}
.framework h3 {
  /*    @include sans-bold;
            @include font-size(16,16);
            color: $turq-medium;
            */
  text-transform: uppercase;
}
.framework div + h3 {
  margin-top: 35px;
}
.framework .wrap {
  padding: 0 30px;
}
.framework .views-row {
  position: relative;
  clear: both;
  min-height: 35px;
  margin: 15px 0;
  display: table;
}
.framework .views-row .views-field-field-icon-svg {
  display: table-cell;
  padding-right: 10px;
  vertical-align: middle;
}
.framework .views-row .views-field-field-icon-svg svg {
  width: 35px;
  height: 35px;
}
.framework .views-row .views-field-title-1 {
  display: table-cell;
  vertical-align: middle;
  width: 140px;
}
.framework .views-row .views-field-title-1 a {
  /*  @include sans-regular;
                    @include font-size(20,22);
                    color: $black;*/
}
.framework .field-name-field-caption {
  /*  margin-top: 30px;
            @include font-size(16,21);
            color: $grey-medium2;
            @include sans-lightitalic;
            strong {
                @include sans-mediumitalic;
            }*/
}
.framework #vis {
  width: 610px;
}
.framework #depth0 {
  fill: #fff;
  font-size: 160%;
  font-weight: bold;
  text-align: center !important;
}
.framework #depth1 {
  font-size: 65%;
  fill: #fff;
  width: 100px !important;
}
.framework #depth2 {
  font-size: 70%;
  fill: #fff;
}
.framework #depth3 {
  font-size: 70%;
  width: 120px !important;
  fill: #2d2019;
}
.framework .sector {
  font-family: centro, sans-serif;
}
.framework path {
  stroke: #fff;
  stroke-width: 1.5;
  cursor: pointer;
}
.framework text {
  font: 11px sans-serif;
  cursor: pointer;
}
.framework body {
  width: 880px;
  margin: 0 auto;
}
.framework h1 {
  text-align: center;
  margin: 0.5em 0;
}
.framework p#intro {
  text-align: center;
  margin: 1em 0;
}
.framework tspan {
  text-align: center !important;
  color: #00f !important;
}
.calculating {
  margin-bottom: 0;
}
.calculating .field-name-field-calculating-intro {
  /*@include sans-light;
            @include font-size(20,24);
            color: $grey-medium;*/
  margin-bottom: 15px;
}
.calculating .field-name-field-calculating-text {
  padding-right: 220px;
}
