.info-container {
  line-height: 1em;
  top: calc(64px + 1em);
  left: 1em;
  position: absolute;
  color: #afafaf;
  transition: all .3s ease;
  transform: translateX(0);
  padding-bottom: 2em;
}
.info-container.out {
  transform: translateX(-380px);
}
.info-container .info-box {
  background-color: #fff;
  width: 360px;
  padding: 2em;
  position: relative;
}
.info-container .info-box:hover.closed {
  transform: translateX(-95%);
}
.info-container .info-box:hover.closed .close-it {
  width: 100%;
}
.info-container .info-box .header {
  padding: 0.5em;
  padding-bottom: 1em;
}
.info-container .info-box .header .title {
  font-weight: normal;
  color: #006bb9;
  padding: 0;
  margin: 0;
  line-height: 0.8em;
  font-size: 1.5em;
}
.info-container .info-box .header .subtitle {
  color: #006bb9;
  line-height: 1em;
  font-size: 0.9em;
}
.info-container .info-box .header .info-icon {
  cursor: pointer;
  fill: #006bb9;
}
.info-container .info-box .header.info {
  border: 0;
  padding: 0.5em;
}
.info-container .info-box .header.info .title {
  line-height: 1.2em;
}
.info-container .info-box .header.info .md-button {
  margin: 0px;
  margin-right: -0.5em;
}
.info-container .info-box .header.center .title {
  margin-top: 0.5em;
  font-size: 1.8em;
  text-align: center;
  line-height: 1em;
}
.info-container .info-box .header.smaller .title {
  font-size: 1.3em;
  font-style: italic;
}
.info-container .info-box .close-it {
  position: absolute;
  top: 0;
  right: 0;
  transition: all 0.25s ease;
  cursor: pointer;
  padding: 0.5em;
  height: 100%;
}
.info-container .info-box .close-it * {
  fill: #d7dddd;
}
.info-container .info-box .close-it:hover * {
  fill: #afafaf;
}
.info-container .info-box .module {
  margin: 1em 0;
  background: #fff;
}
.info-container .info-box .module.no-margin {
  margin: 0px;
}
.info-container .info-box .module.rank .chart {
  color: #333333;
}
.info-container .info-box .module.rank .chart .title {
  margin-bottom: 0.5em;
}
.info-container .info-box .module.rank .chart .data .value {
  border: 2px solid #00ccaa;
  border-radius: 50%;
  height: 80px;
  width: 80px;
}
.info-container .info-box .module.rank .chart .tendency path {
  fill: #d7dddd;
}
.info-container .info-box .module.rank .chart .tendency.active path {
  fill: #00ccaa;
}
.info-container .info-box .module.rank .mini-list {
  font-size: 0.9em;
}
.info-container .info-box .module.rank .mini-list .title {
  margin-bottom: 0.5em;
}
.info-container .info-box .module.rank .mini-list .list {
  height: 3.5em;
  overflow: hidden;
}
.info-container .info-box .module.rank .mini-list .list ul {
  transition: all 0.5s ease-out;
  padding: 0;
  margin: 0;
}
.info-container .info-box .module.rank .mini-list .list ul li {
  list-style-type: none;
  cursor: pointer;
  transition: all 0.5s ease-out;
}
.info-container .info-box .module.rank .mini-list .list ul li.active {
  color: #00ccaa;
}
.info-container .info-box .module.rank .mini-list .list ul li .number {
  margin-right: 0.5em;
}
.info-container .info-box .module:last-child {
  margin-bottom: 0px;
}
.info-container .info-box .module.tabs .header .title {
  opacity: 0.5;
  transition: all 0.2s ease-in-out;
}
.info-container .info-box .module.tabs .header .title.active {
  opacity: 1;
}
.info-container .info-box .module.tabs .header * {
  cursor: pointer;
}
.info-container .info-box .module.tabs .md-tab {
  font-size: 1.2em;
  font-style: italic;
}
.info-container .info-box .module.description .header .sub-title {
  color: #006bb9;
}
.info-container .info-box .module.description .text {
  color: #006bb9;
  margin-bottom: 2em;
  text-align: justify;
  line-height: 1.2em;
}
.info-container .info-box .module.description .caption {
  font-style: italic;
  padding: 1em;
}
.info-container .info-box .module.comparison h4 {
  color: #006bb9;
  font-weight: normal;
  font-size: 1.2em;
  line-height: 1em;
  margin-bottom: 1.5em;
}
.info-container .info-box .module.comparison h4 .number {
  color: #00ccaa;
  font-size: 0.9em;
}
.info-container .info-box .module.comparison .country {
  text-align: center;
}
.info-container .info-box .country_list {
  position: absolute;
  left: 100%;
  top: 0;
  height: 100%;
  background-color: #fff;
  overflow-y: auto;
  width: 300px;
}
.info-container .info-box .country_list md-content {
  height: 100%;
}
.info-container .info-box .md-button {
  border-radius: 20px;
  padding: 0 30px;
  text-transform: none;
  border: 1px solid;
}
.info-container .info-box .md-ripple-container {
  border-radius: 20px;
}
.info-container .info-box.sub-box {
  position: absolute;
  top: 0px;
  width: 295px;
  left: 365px;
  margin-left: 1em;
}
.info-container .info-box.sub-box .close-box {
  margin-bottom: 0px;
  margin-top: -1em;
  margin-left: -1em;
  text-align: left;
  cursor: pointer;
}
.info-container .info-box.sub-box .close-box path {
  fill: #006bb9;
}
.info-container .info-box.sub-box#sub-info {
  width: 700px;
}
.info-container .md-tab {
  text-transform: none;
}
.nvd3 text {
  font-family: Georgia;
}
.nvd3.nv-legend text {
  font-size: 1em;
}
.nvd3 .nv-axis .nv-axisMaxMin text {
  font-weight: normal;
}
.nvd3.nv-sunburst g:first-child text {
  font-size: 2em;
  top: 0.25em;
}
.nvtooltip {
  border-radius: 0px;
}
